import React from 'react';
import NewsCard from '../NewsCard/NewsCard';
import { Grid, Grow, Typography } from '@material-ui/core';
import useStyles from './Styles.js';



const infoCards = [ 
    { color: '#fd9b05', title: 'Latest News', text: 'Give me the latest news' },
    { color: '#0ee589', title: 'News By Categories', info: 'Business, Entertainment, General, Health, Science, Sports, Technology', text: 'Give me the latest Technology news' },
    { color: '#32c9be', title: 'News By Terms', info: 'Bitcoin, PlayStation 5, Smartphones, Politcs, Money...', text: 'What\'s up with Smartphones' },
    { color: '#e4e51e', title: 'News By Sources', info: 'CNN, Wired, BBC News, Time, IGN, Buzzfeed, ABC News...', text: 'Give me the news from CNN' },
]



const NewsCards = ({ articles, activeArticle }) => {
    const classes = useStyles();

    if(!articles.length) { 
        return(
            <Grow in>
                <Grid className ={classes.container} container alignItems="stretch" spacing={3}>
                    {infoCards.map((infoCard) => (
                        <Grid item xs={12} sm={6} md={4} lg={3} className={classes.infoCard}>
                            <div className={classes.card} style={{ backgroundColor: infoCard.color }}>
                                <Typography variant="h5">{infoCard.title}</Typography>
                                {infoCard.info ? (<Typography variant="h6"><strong>{infoCard.title.split(' ')[2]}:</strong><br/>{infoCard.info}</Typography>) : null}
                                <Typography variant="h6">Try Saying: <br/><i>{infoCard.text}</i></Typography>
                            </div>
                        </Grid>
                    ))}
                </Grid>


            </Grow>
        )
    }

    return (

        <Grow in>
            <Grid className={classes.container} container alignItems="stretch" spacing={3}>
                {articles.map((article, i) => (
                    <Grid key={i} item xs={12} sm={6} md={4} lg={3} style={{display: 'flex'}}>
                        <NewsCard article={article} activeArticle={activeArticle} i={i}/>
                    </Grid>
                ))} 
            </Grid>

        </Grow>
    )
}

export default NewsCards;
